import axios from 'axios'
import { baseUrl } from '../config/envconfig'

/**
 * @params method {string} 
 * @params url {string}   /login baseURL
 * @params baseURL {string}  ******  http://cangdu.org
 * @params timeout {number}   30000
 * @params params {object}  getkey
 * @params headers {string} 
 * @params withCredentials {boolean} cookies
 * @params validateStatus {func}  200 - 300
 * @return {Promise}
 * axios 
 * paramsmethod url 2params
 */
export default class Server {
    axios(method, url, data, params) {
        const baseURL = 'http://elm.cangdu.org' + url;
        return new Promise((resolve, reject) => {
            let _option = {
                method,
                url: baseURL,
                baseUrl: baseURL,
                timeout: 30000,
                params,
                data,
                headers: null,
                withCredentials: true, //cookie
                validateStatus: (status) => {
                    return status >= 200 && status < 300
                },
            }
            axios.request(_option).then(res => {
                resolve(typeof res.data === 'object' ? res.data : JSON.parse(res.data))
            }, error => {
                if (error.response) {
                    reject(error.response.data)
                } else {
                    reject(error)
                }
            })
        })
    }
}