import React, {Component} from 'react'
import PropTypes from 'prop-types'
import './index.scss'

class AlertTip extends Component {
  static propTypes = {
    alertText: PropTypes.string.isRequired,  // 
    closeTip: PropTypes.func.isRequired,   // 
    logout: PropTypes.func   // 
  }
  handleClick = () => {  // 
    this.props.closeTip()
  }
  handleLogout = () => {  // 
    this.props.logout()
  }
  render () {
    return (
      <div className='alert-container'>
        <section className='tip-text-container'>
          <div className='tip-icon'>
            <span></span>
            <span></span>
          </div>
          <div className='tip-text'>{this.props.alertText}</div>
          {this.props.logout('wait')? 
            <div className='logout' >
              <div onClick={this.handleClick}></div>
              <div onClick={this.handleLogout}></div>
            </div>
            :<div className='confirm' onClick={this.handleClick}></div>}
          
        </section>
      </div>
    )
  }
}

export default AlertTip