import React, { Component } from 'react';

import Header from '../../components/header/index.jsx';
import logo from '../../assets/images/logo.jpg';
import {Toast } from 'antd-mobile';
import AlertTip from '../../components/alert_tip/index.jsx';
import './index.scss';
export default class Downlaod extends Component {
  state = {
    alertText: 'APP',
  }
  goBack = () => {
    this.props.history.goBack()
  }
  render() {
    return (<div className="download">
      <Header goBack={this.goBack} title={''}></Header>
      <section>
        <img src={logo} alt="" />
        <p>APP</p>
        <div className="determine" onClick={() => {Toast.info('IOSappStore') }}></div>
      </section>
    </div>)
  }
}